:root{
    --text-primary:#000;
    --bgPrimary:#fff;
   --bgSecondary: #F9F9F9;
   --blue: #0071bd;
   --light-blue: #e6f7ff;
   --border: #d9d9d9;
}

*{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif !important;
    box-sizing: border-box;
}

.app{
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.navbar{
    background-color: #001529;
    height: 90px;
    position: fixed;
    left: 0;
    top:0;
    z-index: 20;
    width: 100%;
}

.main{
    width: 100%;
}

.routes{
    margin-top: 100px;
    padding: 20px;
}

.footer{
   background-color: #001529; 
   display: flex;
   flex-direction: column;
   padding: 20px;
   align-items: center;
}
.chart-header{
    display: flex;
    justify-content: space-between;
    grid-gap: 50px;
    gap: 50px;
    color: #0071bd;
}
.chart-title{
    color: #0071bd !important;
}
.price-container{
    display: flex !important;
    grid-gap: 20px !important;
    gap: 20px !important;
    align-items: center !important;
    flex-wrap: wrap !important;
}
.price-change{
    font-weight: 900 !important;
}
.current-price{
    margin-top: 0px !important;
    font-weight: 900 !important;
} 
.coin-detail-container{
    margin: 30px;
}
.coin-heading-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid var(--border);
    padding-top: 10px;
    padding-bottom: 20px;
    grid-gap: 5px;
    gap: 5px;
}
.coin-heading-container .coin-name{
    color: var(--blue);
    font-weight: 700;
}

.coin-heading-container p{
    font-size: 1rem;
    opacity: 0.8;
    margin-bottom: 20px;
}

.select-timeperiod{
    width: 200px;
    margin-top: 20px;
}


.stats-container{
    display: flex;
    align-items: center;
    justify-content: space-evenly; 
    grid-gap: 40px; 
    gap: 40px;
}

.coin-details-heading{
    font-weight: 700 !important;
    margin-top: 20px !important;
    color: var(--blue) !important;
}

.coin-value-statistics-heading p{
    font-size: 1rem;
    opacity: 0.8;   
}

.coin-value-statistics{
    flex: 0.5 1;
}
.other-stats-info{
    flex:0.5 1;
}


/* .stats-container h2{
    margin-top: 20px;
    font-size: 1.4rem;
    font-weight: 700;
    color: var(--blue) !important;
    
} */

/* .coin-value-statistics-heading{
    font-weight: 700;
    margin-top: 20px;
    color: var(--blue) !important;
    
} */


.coin-stats{
    border-bottom: 1px solid var(--border);
    display: flex;
    justify-content: space-between;
    font-size: 1rem;
    opacity: 0.9;
    padding: 20px;
}

.coin-stats-name{
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    font-size: 1rem;
}

.stats{
    font-weight: 700;
}

.coin-desc-link{
    display: flex;
    flex-direction: column;
    grid-gap: 40px;
    gap: 40px;
    margin-top: 40px;
    padding-top: 20px;
}

.coin-desc-link h3{
    font-weight: 700;
    color: var(--pink);
}

.coin-desc-link p{
    font-size: 1rem;
    display: block;
    opacity: 0.8 !important;
}

.link-name{
    text-transform: capitalize;
    font-size: 1rem;
}

.coin-desc-link h3{
    font-weight: 700;
}

.coin-desc-link a{
    color: var(--blue);
}


.coin-desc{
    flex: 0.5 1;
}

.coin-links{
    padding: 0px 20px;
    flex: 0.5 1;

}

.coin-link{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--border);
    padding: 20px;
}

.coin-link a{
    color: var(--blue);
    font-weight: 700;
    font-size: 1rem;
} 

.coin-link:hover, .coin-stats:hover{
    background-color: var(--bgSecondary);
} 




@media screen and (max-width:1000px){
    .stats-container{
        flex-direction: column;
    }
    .coin-desc-link{
        flex-direction: column;
    }
    .stats-container h2{
        margin-top: 0px;
    }
}

@media screen and (max-width: 500px){
    .coin-links{
        padding: 0px;
    }
    .coin-detail-container{
        margin: 0;
    }
}
@media screen and (max-width: 500px){
    .heading{
        margin-top: 20px;
    }
}
